@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;600&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*{
    margin: 0;
    padding: 0;
    font-family: 'Poppins', sans-serif;
}
.app{
    height: 100vh;
    width: 98.9vw;
    background-image: linear-gradient(to right,#F43B86,#3D087B);
}
.flexer{
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-align-items: center;
            align-items: center;
    height: 100vh;
    width: 98vw;
}
.container{
    height: 40vh;
    width: 40vw;
    background-color: white;
    border-radius: 30px;
    display: -webkit-flex;
    display: flex;
}
.left{
    height: 100%;
    width: 20vw;
}
.right{
    height: 100%;
    width: 20vw;
}
.top{
    height: 60%;
    width: 100%;
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: center;
            justify-content: center;
}
.bottom{
    height: 40%;
    width: 100%;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-evenly;
            justify-content: space-evenly;
    -webkit-align-items: center;
            align-items: center;
}
.top>h1{
    font-size: 5rem;

}
.bottom_left>input{
    outline: none;
    height: 3.5vh;
    width: 10vw;
    text-align: center;
    border-radius: 5px;
    border: .2px solid black;
}
.bottom_right>select{
    outline: none;
    background-color: white;
    height: 3.5vh;
    width: 7vw;
    border-radius: 10px;
}
.bottom>h1{
    font-size: 1rem;
}
.top>h1>span{
    font-size: 1.5rem;
}
.devteam{
    height: 15vh;
    width:98vw;
    background-color: white;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-around;
            justify-content: space-around;
    -webkit-align-items: center;
            align-items: center;
    -webkit-flex-direction: column;
            flex-direction: column;
}
.devmembername{
    height: 7vh;
    width: 10vw;
    background-color: black;
    color: white;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    border-radius: 10px;
}
.devmembername:hover{
    -webkit-transform: translateY(-5px);
            transform: translateY(-5px);
    transition: all .5s;
}
.devteam_center{
    display: -webkit-flex;
    display: flex;
    width: 50vw;
    -webkit-justify-content: space-around;
            justify-content: space-around;
    -webkit-align-items: center;
            align-items: center;
    font-size: 0.7rem;
}
.heading{
    font-size: 6rem;
    color: white;
    margin-bottom: 50px;
}
